.tabOne__wrapper {
  width: 1255.5px;
  height: fit-content;
  border: 4px solid black;
  border-top: none;
  border-radius: 0 0 15px 15px;
  background-color: #fff;
  margin: auto;
}

.keyboard {
  margin: auto;
  padding: 10px;
  padding-top: 40px;
  padding-bottom: 40px;
  width: 1100px;
}

@media (max-width: 1455px) {
  .keyboard {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.keyboard ul {
  display: flex;
  position: relative;
}

.keyboard ul * {
  margin-right: 10px;
}

.keyboard h2 {
  margin-top: 12px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
}

.keyboard h3 {
  font-size: 20px;
  position: absolute;
  left: 38px;
  bottom: 5px;
  font-weight: 500;
}

.keyRow {
  margin-top: 10px;
  overflow: visible;
}

.enter__wrapper {
  position: relative;
  transition: all 0.4s;
}

.enter {
  transition: all 0.4s;
  position: absolute;
  right: 0px;
  top: 0px;
  max-height: 140px;
}
